<template>
  <div class="quickSearch">
    <!-- 標題 -->
    <div class="row text-center" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >住家水資訊快速查詢區</span
        >
      </div>
    </div>
    <div
      class="searchCard"
      :style="
        waterPool.length === 0
          ? { 'margin-bottom': '150px' }
          : { 'margin-bottom': '10px' }
      "
    >
      <div class="remindText">
        ※ 本區可以查詢離住家最近的最新相關水資訊<br />
        ( 包含 : 水庫、汙水處理廠、環境水體監測站、地下水監測站、淨水廠 )
      </div>
      <el-row :gutter="20" class="inputHeader">
        <el-col :xs="24" :span="10" class="inputhome">請輸入住家地址</el-col>
        <el-col :xs="24" :span="10"
          ><el-input v-model="address" placeholder="地址"></el-input
        ></el-col>
        <el-col :xs="24" :span="4" style="text-align: center"
          ><el-button @click="search">查詢</el-button></el-col
        >
      </el-row>
      <!-- 查詢後的資料 -->
      <div v-if="stationlatlng.length !== 0">
        <!-- 圖標說明 -->
        <el-card class="iconExplainCard">
          <el-row class="iconExplainCard_row">
            <el-col :xs="24" :span="6" class="iconExplainCard_title">
              圖標說明
            </el-col>
            <el-col :xs="24" :span="18">
              <el-row>
                <el-col
                  :xs="24"
                  :span="15"
                  v-for="(item, index) in iconimg"
                  :key="item"
                  class="iconExplain"
                >
                  <img :src="item" class="iconExplain_img" />
                  <span>{{ iconExplain[index] }}</span>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </el-card>
        <!-- 地圖 -->
        <l-map class="map" :zoom="zoom" :options="options" :center="center">
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="(item, index) in stationlatlng"
            :key="item.name"
          >
            <!-- 地標圖示 -->
            <l-icon :icon-url="iconColor(index)" />
            <l-tooltip :options="tooltipOptions">{{ item.name }}</l-tooltip>
            <!-- 點擊圖標會彈出顯示的文字(地址) -->
            <l-popup>{{ item.address }}</l-popup>
          </l-marker>
        </l-map>
        <el-row>
          <!-- 水庫 -->
          <el-col :xs="24" :span="12" v-if="waterPool.length !== 0">
            <div class="waterpool">
              <div class="childtitle">水庫</div>
              <div id="w1">{{ waterPool[0].name }}</div>
              <el-row style="text-align: center">
                <el-col :span="12">
                  <div><span class="bigWord_before">原本</span>水庫資訊</div>
                  <div id="originalpic">
                    {{ waterPool[0].capacitym3 }}m
                    <sup>3</sup>
                  </div>
                  <div>水位 (m) : {{ waterPool[0].capacitym }}m</div>
                  <div>
                    水庫容量 (萬立方公尺) : {{ waterPool[0].capacitym3 }}m
                    <sup>3</sup>
                  </div>
                </el-col>
                <el-col :span="12">
                  <div><span class="bigWord_after">目前</span>水庫資訊</div>
                  <canvas id="sineCanvas"></canvas>
                  <div id="percent1">
                    有效容量比 : {{ waterPool[0].percent }} %
                  </div>
                  <div>水位 (m) : {{ waterPool[0].nowm }}m</div>
                  <div>
                    水庫容量 (萬立方公尺) : {{ waterPool[0].nowm3 }}m
                    <sup>3</sup>
                  </div>
                  <div>更新時間 : {{ waterPool[0].time }}</div>
                </el-col>
              </el-row>
            </div>
          </el-col>
          <!-- 淨水廠 -->
          <el-col :xs="24" :span="12">
            <div class="cleanwater">
              <div v-if="cleanWater.length !== 0">
                <div class="childtitle">淨水廠</div>
                <div id="w2">
                  {{ cleanWater[0][0].name }}<br />
                  是否通過檢驗 : {{ cleanWater[0][0].pass }}
                </div>
                <el-table :data="cleanWater[0]" stripe fit class="dataTable">
                  <el-table-column prop="type" label="種類"></el-table-column>
                  <el-table-column prop="dirty" label="濁度"></el-table-column>
                  <el-table-column prop="ph" label="PH值"></el-table-column>
                  <el-table-column
                    prop="ceil"
                    label="大腸桿菌值"
                  ></el-table-column>
                </el-table>
                <div style="margin: 20px 0 0 70%">
                  更新時間 : {{ cleanWater[0][0].updatetime }}
                </div>
              </div>
            </div>
            <!-- 汙水處理廠 -->
            <div v-if="dirtyWater.length !== 0">
              <div class="dirtywater">
                <div class="childtitle">汙水處理廠</div>
                <div id="w3">
                  {{ dirtyWater[0].廠區 }}
                </div>
                <el-row :gutter="20" style="text-align: center">
                  <el-col :span="12"
                    >平均噸數 : {{ dirtyWater[0].平均噸數 }}</el-col
                  >
                  <el-col :span="12"
                    >處理等級 : {{ dirtyWater[0].處理等級 }}</el-col
                  >
                </el-row>
                <!-- 處理等級介紹 -->
                <el-card
                  class="dirtyWaterCard"
                  v-if="dirtyWater[0].處理等級 === '初級處理'"
                >
                  <div slot="header" class="clearfix">
                    <span>第一級 ( 初級 ) 處理</span>
                  </div>
                  <div>
                    去除污水中呈懸浮狀況的
                    <b style="color: red">固體污染物質</b
                    >，物理處理法大部分只能完結一級處理的要求。
                    <br />通過一級處理的污水，生化需氧量 (BOD)
                    一般可去除30%左右，但仍達不到排放規範。
                  </div>
                </el-card>
                <el-card
                  class="dirtyWaterCard"
                  v-if="dirtyWater[0].處理等級 === '生物二級處理'"
                >
                  <div slot="header" class="clearfix">
                    <span>第二級處理</span>
                  </div>
                  <div>
                    去除污水中呈膠體和溶解狀況的
                    <span style="color: red; font-weight: 600"
                      >有機污染物質</span
                    >。 <br />
                    1. 生化需氧量( BOD ) <br />2. 化學需氧量( COD
                    )，去除率可達90%以上，使有機污染物到達排放規範。
                  </div>
                </el-card>
                <el-card
                  class="dirtyWaterCard"
                  v-if="dirtyWater[0].處理等級 === '三級處理'"
                >
                  <div slot="header" class="clearfix">
                    <span>第三級處理</span>
                  </div>
                  <div>
                    進一步處理難降解的有機物、氮和磷等，可以導致水體富營養化的
                    <b style="color: red">可溶性無機物</b>等。 <br />首要辦法有
                    :
                    <br />生物脫氮除磷法、混凝沉澱法、砂濾法、活性炭吸附法、離子交換法，和電滲分析法等。
                  </div>
                </el-card>
              </div>
            </div>
          </el-col>
        </el-row>
        <!-- 環境水體監測站 -->
        <el-row :gutter="20">
          <el-col :span="24">
            <div class="environment">
              <div class="childtitle">環境水體監測站</div>
              <div id="w4">
                {{ AllName.environmentData }}
              </div>
              <div>
                <b>監測河川名稱：{{ environmentRiver }}</b>
              </div>
              <el-row style="margin: 20px 0">
                <el-col :xs="24" :span="8"
                  ><div>
                    ※ 若欲查詢各測站的詳細監測資料，請至以下兩個網站查詢
                  </div></el-col
                >
                <el-col :xs="24" :span="8"
                  >1.
                  <a
                    href="https://ksenlab.ksepb.gov.tw/kh-taqm/zh-tw/WaterData.aspx"
                    >高雄市政府環境保護局環境檢驗科</a
                  ></el-col
                >
                <el-col :xs="24" :span="8"
                  >2.
                  <a
                    href="https://wq.epa.gov.tw/Code/AdvSearch.aspx?YearSelect=3&StartYear=&EndYear=&Area=1660&PageID=5"
                    >行政院環境保護署全國環境水質監測資訊網</a
                  ></el-col
                >
              </el-row>
              <div v-if="NewRiverChart.yAxis.data.length !== 0">
                <div>
                  <b>{{ nowYear }}年 {{ environmentRiver }}汙染狀態</b>
                </div>
                <chart
                  :options="NewRiverChart"
                  height="300"
                  style="width: 95%; height: 150px"
                ></chart>
              </div>
            </div>
          </el-col>
        </el-row>
        <!-- 地下水監測站 -->
        <el-row :gutter="20">
          <el-col :span="24">
            <div class="groundwater">
              <div class="childtitle">地下水監測站</div>
              <div id="w5">
                {{ AllName.groundWaterData }}
              </div>
              <!-- 顏色對照檢測是否通過說明 -->
              <el-row v-if="underWater.length" class="colorExplain">
                <el-col :xs="3" :span="5" style="padding: 0px 2% 0px 0px">
                  <div class="passBlock"></div>
                </el-col>
                <el-col :xs="9" :span="7">
                  <span>通過監測標準</span>
                </el-col>
                <el-col :xs="3" :span="5" style="padding: 0px 2% 0px 0px">
                  <div class="unpassBlock"></div>
                </el-col>
                <el-col :xs="9" :span="7">
                  <span>未通過監測標準</span>
                </el-col>
              </el-row>
              <!-- 表格資訊 -->
              <div v-for="item in underWater" :key="item.name">
                <div style="margin: 10px 0px 0px; 0px;;">
                  <span
                    ><b>測站名稱：{{ item.name }}</b></span
                  ><br />
                  <span class="groundWaterdate"
                    >檢測日期 : {{ item.date }}</span
                  >
                </div>
                <el-table
                  :data="[item]"
                  style="width: 100%"
                  :header-cell-style="{
                    background: '#5F5F5F',
                    color: 'white',
                    fontSize: '18px',
                    textAlign: 'center',
                  }"
                  :cell-style="tableCellStyle"
                >
                  <el-table-column prop="item1" label="鐵"> </el-table-column>
                  <el-table-column prop="item2" label="錳"> </el-table-column>
                  <el-table-column prop="item3" label="總硬度">
                  </el-table-column>
                  <el-table-column prop="item4" label="總溶解固體物">
                  </el-table-column>
                  <el-table-column prop="item5" label="氯鹽"> </el-table-column>
                  <el-table-column prop="item6" label="氨氮"> </el-table-column>
                  <el-table-column prop="item7" label="硫酸鹽">
                  </el-table-column>
                  <el-table-column prop="item8" label="總有機碳">
                  </el-table-column>
                  <el-table-column prop="item9" label="總酚"> </el-table-column>
                </el-table>
              </div>
              <!-- 無資料顯示字樣 -->
              <div v-if="underWater.length === 0" class="noNewestData">
                目前無{{ AllName.groundWaterData }}最新的檢測資訊
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import moment from "moment";
export default {
  data() {
    return {
      address: "高雄市燕巢區西燕里工程路100號",
      // address: "高雄市左營區博愛二路777號",
      latlng: [],
      sumlatlng: 0,
      minusNum: 0,
      AllName: {
        waterPoolData: "",
        dirtyWaterData: "",
        environmentData: "",
        groundWaterData: "",
        cleanWaterData: "",
      },
      environmentRiver: "",
      nowYear: "",
      nowYear2: "",
      allSiteData: [],
      stationlatlng: [],
      waterPool: [],
      cleanWater: [],
      dirtyWater: [],
      underWater: [],
      // 高雄主要河川目前最新污染表格資料
      NewRiverChart: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {
          data: [
            "嚴重污染長度",
            "中度污染長度",
            "輕度污染長度",
            "未(稍)受污染長度",
          ],
        },
        grid: {
          left: "1%",
          right: "2%",
          bottom: "1%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
        },
        yAxis: {
          type: "category",
          data: [],
        },
        series: [
          {
            name: "未(稍)受污染長度",
            type: "bar",
            stack: "总量",
            color: "#91c7ae",
            data: [],
          },
          {
            name: "輕度污染長度",
            type: "bar",
            stack: "总量",
            color: "#61a0a8",
            data: [],
          },
          {
            name: "中度污染長度",
            type: "bar",
            stack: "总量",
            color: "#d48265",
            data: [],
          },
          {
            name: "嚴重污染長度",
            type: "bar",
            stack: "总量",
            color: "#c23531",
            data: [],
          },
        ],
      },
      // cleanwaterStandeardData: [],
      // tableData: [],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 11,
      //地圖中心
      center: [22.612786, 120.442959],
      // 地圖圖標說明
      iconExplain: [
        "水庫",
        "地下水監測站",
        "環境水體監測站",
        "地下水監測站",
        "淨水廠",
      ],
      // 地標圖示
      iconimg: [
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-yellow.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-black.png",
      ],
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [10, 10],
      },
    };
  },
  mounted() {
    this.nowYear = moment(new Date()).format("YYYY") - 1910; // 取得今年的民國年分
    this.nowYear2 = moment(new Date()).format("YYYY"); // 取得今年的民國年分
    axios
      .get("server/quickSearch.json")
      .then((res) => {
        this.allSiteData = res.data;
        console.log(this.allSiteData);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    // 根據水庫的目前有效容量比，更改地圖上icon顏色
    iconColor(index) {
      if (index === 0) {
        return this.iconimg[0];
      }
      if (index === 1) {
        return this.iconimg[1];
      }
      if (index === 2) {
        return this.iconimg[2];
      }
      if (index === 3) {
        return this.iconimg[3];
      }
      if (index === 4) {
        return this.iconimg[4];
      }
    },
    search() {
      this.latlng = [];
      this.stationlatlng = [];
      //  若查詢地址非高雄，跳出提醒訊息並return
      if (this.address.indexOf("高雄") === -1) {
        this.$message({
          message: "查詢失敗!! 抱歉目前此功能只開放高雄地區查詢",
          type: "warning",
        });
        return;
      }
      //   用geocoder將地址轉為經緯度
      this.$geocoder.setDefaultMode("address");
      var addressObj = {
        address_line_1: this.address,
      };
      this.$geocoder.send(addressObj, (response) => {
        console.log(response);
        // 若地址亂打，google依舊會自動辨識地址，為避免行政區打高雄，但後面地址為其他縣市有的地方(ex:高雄市大安區羅斯福路四段1號)
        // 這邊會再判斷一次結果中的地址是否為高雄
        if (response.results[0].formatted_address.indexOf("高雄") === -1)
          return;
        this.latlng.push(response.results[0].geometry.location.lat);
        this.latlng.push(response.results[0].geometry.location.lng);
        console.log(this.latlng);
        // 將經緯度相加
        this.sumlatlng = this.latlng[0] + this.latlng[1];
        console.log(this.sumlatlng);
        // 找尋水庫、汙水處理廠、環境水體監測站、地下水監測站、淨水廠離查詢地址最近的監測站
        for (let key in this.allSiteData[0]) {
          console.log(key);
          console.log(this.allSiteData[0][key]);
          var mapdetail;
          this.allSiteData[0][key].forEach((item, index) => {
            let sum = item.local[0] + item.local[1];
            let minus = sum - this.sumlatlng;
            if (index === 0) {
              this.minusNum = Math.abs(minus);
              this.AllName[key] = item.name;
              mapdetail = {
                name: item.name,
                address: item.SiteAddress,
                local: item.local,
                type: key,
              };
            }
            if (this.minusNum > Math.abs(minus)) {
              this.minusNum = Math.abs(minus);
              this.AllName[key] = item.name;
              if (key === "environmentData") {
                this.environmentRiver = item.River;
              }
              mapdetail = {
                name: item.name,
                address: item.SiteAddress,
                local: item.local,
                type: key,
              };
            }
            if (index == this.allSiteData[0][key].length - 1) {
              this.stationlatlng.push(mapdetail);
            }
          });
        }
        console.log(this.AllName);
        console.log(this.stationlatlng);
        this.getWaterPool();
        this.getCleanWater();
        this.getDirtyWater();
        this.getEnvironmentWater();
        this.getUnderWater();
      });
    },
    getWaterPool() {
      axios
        .get("server/water.json")
        .then((res) => {
          this.waterPool = res.data;
          this.waterPool = this.waterPool.filter((item) => {
            return item.name === this.AllName.waterPoolData;
          });
          console.log(this.waterPool);
          // 改變各水庫有效容量比顏色
          window.setTimeout(() => {
            var percent1 = document.getElementById("percent1");
            if (this.waterPool[0].percent < 30) {
              percent1.style.color = "red";
            } else if (
              this.waterPool[0].percent > 30 &&
              this.waterPool[0].percent < 80
            ) {
              percent1.style.color = "#DAA520";
            } else {
              percent1.style.color = "green";
            }
            this.drawWater(this.waterPool[0].percent);
          }, 0);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getCleanWater() {
      let cleanWaterData = [];
      this.cleanWater = [];
      axios
        .get("server/cleanwater2.json")
        .then((res) => {
          console.log(res.data);
          if (res.data.length) {
            cleanWaterData = res.data;
            cleanWaterData = cleanWaterData.filter((item) => {
              return item.name === this.AllName.cleanWaterData;
            });
          }
          console.log(cleanWaterData);
          let obj = [
            {
              type: "測值",
              name: cleanWaterData[0].name,
              pass: cleanWaterData[0].pass,
              dirty: cleanWaterData[0].dirty,
              ceil: cleanWaterData[0].ceil,
              ph: cleanWaterData[0].ph,
              updatetime: cleanWaterData[0].updatetime,
            },
            {
              type: "最大限值",
              dirty: "2",
              ceil: "6.0-8.5",
              ph: "6",
            },
          ];
          this.cleanWater.push(obj);
          console.log(this.cleanWater);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getDirtyWater() {
      axios
        .get("server/dirtywater.json")
        .then((res) => {
          this.dirtyWater = res.data;
          this.dirtyWater = this.dirtyWater.filter((item) => {
            return item.廠區 === this.AllName.dirtyWaterData;
          });
          console.log(this.dirtyWater);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async getEnvironmentWater() {
      this.NewRiverChart.yAxis.data = [];
      if (
        this.environmentRiver.indexOf("高屏溪") !== -1 ||
        this.environmentRiver.indexOf("阿公店溪") !== -1 ||
        this.environmentRiver.indexOf("二仁溪") !== -1 ||
        this.environmentRiver.indexOf("鳳山溪") !== -1
      ) {
        axios
          .post(`${this.allURL}environmentAPI/newestRiverData`, {
            year: this.nowYear,
            river: this.environmentRiver,
          })
          .then((res) => {
            console.log(res.data);
            this.NewRiverChart.yAxis.data.push(res.data.data.item2);
            this.NewRiverChart.series[0].data.push(res.data.data.value2);
            this.NewRiverChart.series[1].data.push(res.data.data.value3);
            this.NewRiverChart.series[2].data.push(res.data.data.value4);
            this.NewRiverChart.series[3].data.push(res.data.data.value5);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 取得檢測資訊
    async getUnderWater() {
      this.underWater = [];
      axios
        .post(`${this.allURL}groundWater/newestGroundData`, {
          sitename: this.AllName.groundWaterData,
          year: this.nowYear2,
        })
        .then((res) => {
          console.log(res.data);
          this.underWater = res.data.newestData;
          console.log(this.underWater);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 地下水表格cell設定
    tableCellStyle({ row, columnIndex }) {
      let cellStyle;
      if (columnIndex === 0 && row.year !== undefined) {
        return (cellStyle = "background: #fffff0");
      }
      let index = 0;
      if (row.year !== undefined) {
        index = columnIndex;
      } else {
        index = columnIndex + 1;
      }
      let firstWWord = row[`item${index}`].substring(0, 1);
      let value = 0;
      value = parseFloat(row[`item${index}`]);
      if (firstWWord === "<" || firstWWord === ">") {
        value = parseFloat(row[`item${index}`].substr(1));
      }
      let standard = [0.15, 0.025, 150, 250, 125, 0.05, 125, 2.0, 0.014];
      if (value < standard[index - 1]) {
        cellStyle = "background: #99DCF1";
      } else if (isNaN(value)) {
        cellStyle = "background: white";
      } else {
        cellStyle = "background: #FBD8D8";
      }
      return cellStyle;
    },
    //水填滿canvas
    drawWater(percent) {
      var unit = 100,
        canvas,
        context,
        height,
        width,
        xAxis,
        yAxis;
      var percentMax = percent / 100; //液體占容器的百分比
      var percentMin = 0;
      var maxHeight = 1;
      if (percentMax >= 1) {
        maxHeight = 0.95;
      }
      var wavePower = 3;
      //初始化
      function init() {
        window.requestAnimFrame = (function () {
          return (
            window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            function (callback) {
              window.setTimeout(callback, 1000 / 60);
            }
          );
        })();
        canvas = document.getElementById("sineCanvas");
        canvas.width = 1000;
        canvas.height = 1000;
        context = canvas.getContext("2d");
        height = canvas.height;
        width = canvas.width;
        yAxis = 0;
        draw();
      }
      function draw() {
        // 清理canvas畫板
        context.clearRect(0, 0, width, height);
        //描繪波浪
        if (percent > 80) {
          drawWave(["#77DDFF", "#0044BB"], 1, 1.5);
        } else if (percent > 30 && percent < 80) {
          drawWave(["#F0E68C", "#B8860B"], 1, 1.5);
        } else {
          drawWave(["#FFA07A", "#FF4500"], 1, 1.5);
        }
        if (percentMin <= percentMax) {
          percentMin = percentMin + 0.002;
          xAxis = Math.floor(height * (maxHeight - percentMin));
        }
        if (percentMin > 0.8) {
          if (wavePower <= 36 * percentMax) {
            wavePower = wavePower + 0.05;
          }
        }
        // 更新時間然後重新繪制
        draw.seconds = draw.seconds + 0.009;
        draw.t = draw.seconds * Math.PI;
        window.requestAnimFrame(draw);
      }
      draw.seconds = 0;
      draw.t = 0;
      /**
       * 描繪波浪
       * drawWave([漸變左側颜色，右側颜色], 不透明度, 波浪的幅度)
       */
      function drawWave(color, alpha, zoom) {
        context.globalAlpha = alpha;
        var linearGrad = context.createLinearGradient(0, 0, width, 0);
        linearGrad.addColorStop(0.0, color[0]);
        linearGrad.addColorStop(1.0, color[1]);
        context.fillStyle = linearGrad;

        context.beginPath(); //開始路徑
        drawSine(draw.t / 0.5, zoom);
        context.lineTo(width + 10, height); //路徑朝canvas右下
        context.lineTo(0, height); //路徑朝canvas左下
        context.closePath(); //封閉路徑
        context.fill(); //塗色
      }
      /**
       * 畫正弦
       * drawSine(時間, 波浪的浮動)
       */
      function drawSine(t, zoom) {
        var x = t; //將時間轉換為x軸
        var y = Math.sin(x) / zoom;
        context.moveTo(yAxis, unit * y + xAxis); //在開始位置放置路径
        // 繪制波浪、横向幅度
        for (var i = yAxis; i <= width + 10; i += 10) {
          x = t + (-yAxis + i) / unit / zoom;
          y = Math.sin(x - 0) / wavePower;
          context.lineTo(i, unit * y + xAxis);
        }
      }
      init();
    },
  },
};
</script>
